.header-main {
  width: 100vw;
  height: 6vh;
  border-bottom: 2px solid #ddd92a;
  border-top: 2px solid #ddd92a;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  background-color: #17161a;

}

.redirect {
  padding: 5px;
  margin: 5px;
  /* border-radius: 15px; */
  font-size: 2vh;
  transition: 0.5s;
  border: 0;
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.redirect:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #ddd92a;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.redirect:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
